___CSS_LOADER_EXPORT___.push([module.id, `* {
  user-select: none;
}
body,
html {
  background-color: black;
}
.button,
.button2 {
  padding: 10px 30px;
  font-size: 15px;
}

#list-items,
#list-items2 {
  display: none;
  position: absolute;
  background-color: white;
  min-width: 185px;
}

#list-items a,
#list-items2 a {
  display: block;
  font-size: 18px;
  background-color: #ddd;
  color: black;
  text-decoration: none;
  padding: 10px;
}

#btnHolder {
  display: flex;
  justify-content: center;
  gap: 20px;
}

#pictureContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 10px;
}

#lamar {
  height: 25rem;
  width: 40rem;
  transition: opacity 2s;
  opacity: 1;
}

.animate {
  transition: opacity 2s;
  opacity: 1;
}

#circleContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  height: 5rem;
  width: 40rem;
  margin: 0px auto;
  gap: 5px;
}

.circle {
  border: 1px solid black;
  background-color: white;
  height: 2rem;
  width: 2rem;
  border-radius: 50%;
}

#backBtn,
#forwardBtn {
  background-color: rgb(0, 0, 0);
  color: white;
  border: none;
  font-size: 3rem;
  opacity: 0.3;
}

#backBtn:hover,
#forwardBtn:hover {
  opacity: 1;
  color: white;
}
